import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.for-each.js";
import { defineStore } from "pinia";
import { routes } from "@/router/routes";
/**
 * @description: 路由-store
 */
export const useRouterStore = defineStore("router", () => {
  const whiteList = ref(["login", "notFound"]); // 路由白名单
  const keepAliveList = ref([]); // 缓存路由列表
  const getKeepAliveList = routes => {
    const keepAliveNameList = [];
    routes.forEach(route => {
      if (route.meta?.keepAlive) {
        keepAliveNameList.push(route.name);
      }
      if (route.children) {
        keepAliveNameList.push(...getKeepAliveList(route.children));
      }
    });
    return keepAliveNameList;
  };
  keepAliveList.value = getKeepAliveList(routes);
  return {
    whiteList: readonly(whiteList),
    keepAliveList
  };
});